                                        <i class="el-icon-tickets" title="" @click.stop="handleOtherTable(scope.row,scope.$index)"></i>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div v-if="showTable" style="margin-top: 10px;">
                                <CommonTable
                                    v-if="isResize"
                                    ref="tabTableII"
                                    :isCenter="true"
                                    :isMulTable=true
                                    :otherVariable="otherVariable"
                                    :formTableData="tabDataIII"
                                    :formPropTable="tabPropIII"
                                    :tableHeight="tableHeightII"
                                >
                                </CommonTable>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                mainHeight: 716,
                isResize: true,
                loadingI:false,
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                currentIndex: 0,
                clickTimes: 0,
                editIndex: 0,
                tableHeightI: this.$store.state.screenHei*0.62,
                tableHeightII: 150,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    //isMulSelect: true,
                    isInlineForm: true,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    isOtherTable: true
                },
                showTable: false,
                tempWidth: null,
                tableData: [],
                showPrice: false,
                showUnPrice: false,
                priceUomList:[
                    {
                        value: 'kg',
                        label: 'kg'
                    }
                ],
                areaPriceUomList:[
                    {
                        value: 'kg',
                        label: 'kg'
                    },
                    {
                        value: 'm',
                        label: 'm'
                    },

                ],
                //
                searchProp: [
                    {
                        name:'',
                        prop:'refDocCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'clientCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'clientName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:0,label:''},{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                returnStatusName: {
                    '0':'',
                    '50':'',
                    '100':'',
                    'null':''
                },
                //
                formProp: [
                    {name:'',prop:'refDocCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'deliveryDate',type:'outter'},
                    {name:'',prop:'priceStatus',type:'outter',isStatus:true},
                    {name:'',prop:'isCheckPrice',type:'outter',isCheck:true},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'contractCode',type:'inner'},
                    {name:'',prop:'alPriceMethodName',type:'inner'},
                    {name:'',prop:'alPrice',type:'inner'},
                    {name:'',prop:'companyName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'settlementMethodName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'usrTxt1Name',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'refDocCode',
                        name2:'',
                        prop2:'priceStatus',
                        type:'is_check_price',
                        width: ''
                    },
                    {
                        name1:'/',
                        prop1:'docType',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'enterName',
                        name2:'',
                        prop2:'enterTime',
                        type:'mixin',
                        width: ''
                    }
                ],
                searchForm: {
                    minPriceStatus: 0,
                    maxPriceStatus: 50
                },
                detailForm: {},
                priceIdsArray:[],
                detailId: null,
                refMatPrice: null,
                refMatProcessPrice: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                unitList: [],
                //
                tabLeft: '100px',
                activeName: 'first',
                //
                tabDataI: [],
                copyDataI: [],
                isShowNotAllowed: false,
                tabPropIII: [
                    {
                        prop:'itemNo', name:'',
                    },

                    {prop:'priceItemText', name:'',type:'normal',width:'100'},

                    {
                        prop:'', name:'',
                        children:[
                            {prop:'otherPrice',name:'(/m)',type:'normal'},
                            {prop:'price',name:'(/kg)',type:'normal'},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },

                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                ],
                tabDataIII:[]
            }
        },
        watch: {
            'otherVariable.isChildEdit':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            }
        },
        mounted() {
            this.getData();
            this.getUnitList();
            // /
            this.listenResizeIII();
            this.changeSizeIII();
            this.tempWidth = this.otherVariable.contentWidth;
            //
            this.$refs.pricedetail.$el.oncontextmenu = function() {return false};
        },
        methods: {
            //
            getData(isFirst = true) {
                this.tableData = [];
                this.showTable = false;
                this.fetch(`/oms/sls/SalesOrderPriceController/getOrderAndContractInfo?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                        }
                    })
            },
            selectChange(val) {
                if(val === 0) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 101;
                }else if(val === 1) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 50;
                }else if(val === 2) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minPriceStatus = 100;
                    this.searchForm.maxPriceStatus = 100;
                }
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData()
            },
            //
            handleRefresh() {
                this.getData();
                this.otherVariable.isChildEdit = false;
                this.tabDataI = [];
                this.showPrice = false;
                this.showUnPrice = false;
                this.detailForm = {};
            },
            //
            handleReset() {
                this.searchForm = {minPriceStatus: 0,maxPriceStatus: 50};
                this.handleRefresh();
            },
            //
            handleDetail(row) {
                if(row.priceStatus === 0 || row.priceStatus === null) {
                    this.showPrice = true;
                    this.showUnPrice = false;
                }else if(row.priceStatus === 50){
                    this.showPrice = false;
                    this.showUnPrice = true;
                }else {
                    this.showPrice = false;
                    this.showUnPrice = false;
                }
                this.isShowStatus.isRead = true;
                this.detailForm = row;
                this.refMatPrice = row.alPrice;
                this.showTable = false;
                this.tabDataIII = [];
                this.detailId = row.fid;
                this.getDetailTable(row.fid)
            },
            //
            getDetailTable(fid) {
                this.tabDataI = [];
                this.copyDataI = [];
                this.priceIdsArray = [];
                this.loadingI=true;
                this.otherVariable.isChildEdit = false;
                this.isShowNotAllowed = true;
                this.fetch('/oms/sls/SalesOrderPriceController/getMatOrderAndPrice', {flowId: fid})
                    .then(result => {
                        this.isShowNotAllowed = false;
                        if(result.status === 200 && result.data) {
                            result.data.forEach(item => {
                                item.pricePart8 = this.refMatPrice;
                                item._curdFlag = 'r';
                                item === null ?item = '' : '';
                                this.priceIdsArray.push(item.priceId);
                            });
                            this.tabDataI = result.data;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                        }else {
                            this.tabDataI = [];
                            this.copyDataI = [];
                        }
                    }).then(()=>{
                    this.loadingI = false;
                });
            },
            //
            handlePrice() {
                let resultStatus;
                this.$confirm('?','',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'

                }).then(()=>{
                    this.fetch('/oms/sls/SalesOrderPriceController/submitPrice', {orderFlowId: this.detailId,priceStatus:50,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message:'',
                                    type:'success'
                                });
                                this.getData();
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error),
                                    type:'error'
                                })
                            }
                        })

                });

            },
            //
            handldUnPrice() {
                let resultStatus;
                this.fetch('/oms/sls/SalesOrderPriceController/submitPrice', {orderFlowId: this.detailId,priceStatus:0,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                    .then(result => {
                        resultStatus = result.status;
                        if(result.status === 200) {
                            this.$message({
                                message:'',
                                type:'success'
                            });
                            this.getData();
                        }else {
                            this.$message({
                                message:this.returnErrorMsg(result.error),
                                type:'error'
                            })
                        }
                    })
            },
            //
            clickDetail(row) {
                if(this.detailForm.isCheckPrice !== 1) {
                    this.showTable = false;
                    if(row.isLocked !== 1 && !this.otherVariable.isReadTable && this.showPrice) {
                        this.otherVariable.isChildEdit = true;
                        this.tabDataI = this.giveState(this.tabDataI);
                        this.copyDataI = this.giveState(this.copyDataI);
                        row.isEditState = true;
                    }
                    this.childDetailRow = row || {};
                    this.clickTimes += 1;
                    if(this.clickTimes === 1) {
                        this.refMatProcessPrice = this.$lodash.cloneDeep(this.childDetailRow).pricePart1;
                    }
                    this.editIndex = this.$lodash.findIndex(this.tabDataI, ['priceId', row.priceId]);
                    if(this.editIndex < 0) {
                        this.editIndex = 0;
                    }
                }
            },
            //
            handleSaveChild() {
                let tempList = [];
                let priceOperateModels = [];
                let oldItem = this.$lodash.cloneDeep(this.copyDataI);
                let newItem = this.$lodash.cloneDeep(this.tabDataI);
                oldItem.forEach(item => {
                    item.isEditState = false;
                });
                newItem.forEach(item => {
                    item.isEditState = false;
                });
                tempList = this.getLastList(oldItem,newItem,'variantId');
                let tempObj;
                let api = '/oms/sls/SalesOrderPriceController/updatePcfMatPrice';
                tempList.forEach(item => {
                    let model = {};
                    console.log(item);
                    model.areaPriceUom = item.areaPriceUom;
                    model.priceUom = item.priceUom;
                    model.sjgPriceUom = item.sjgPriceUom;
                    model.variantId = item.variantId;
                    model.sjgPrice = item.sjgPrice;
                    model.jpPrice= item.jpPrice;
                    model.jpPriceUom = item.jpPriceUom;
                    model.contractId = null;
                    model.contractCode = null;
                    model.alPriceDate = null;
                    model.fid = item.priceId;
                    model.isLocked = item.isLocked ? 1 : 0;
                    model.price = Number(item.price);
                    model.refMatPrice = Number(item.pricePart1);
                    model.refMatProcessPrice = Number(item.pricePart2);
                    model.otherPrice = Number(item.otherPrice);
                    model.pricePart1 = Number(item.pricePart1);
                    model.pricePart2 = Number(item.pricePart2);
                    model.pricePart3 = Number(item.pricePart3);
                    model.pricePart4 = Number(item.pricePart4);
                    model.usrPricePart1 = Number(item.usrPricePart1);
                    model.usrPricePart2 = Number(item.usrPricePart2);
                    model.usrPricePart3 = Number(item.usrPricePart3);
                    model.usrPricePart4 = Number(item.usrPricePart4);
                    model.usrPricePart5 = Number(item.usrPricePart5);
                    model.usrPricePart6 = Number(item.usrPricePart6);
                    model.usrPricePart7 = Number(item.usrPricePart7);
                    model.usrPricePart8 = Number(item.usrPricePart8);
                    model.usrPricePart9 = Number(item.usrPricePart9);
                    model.usrPricePart10 = Number(item.usrPricePart10);
                    priceOperateModels.push(model);
                });
                tempObj = {
                    orderFlowId: this.detailForm.fid,
                    priceOperateModels: priceOperateModels
                };
                this.fetch(api,tempObj)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                            this.clickTimes = 0;
                        }else {
                            this.$message({
                                message: this.returnErrorMsg(result.error),
                                type: 'error'
                            });
                        }
                    });
                this.tabDataI = this.giveState(this.tabDataI)
            },
            //
            handleLock() {
                this.getAjax(`/oms/sls/SalesOrderPriceController/doLockPrice?priceId=${this.childDetailRow.priceId}`)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                        }else {
                            this.$message({
                                message: '',
                                type: 'error'
                            });
                        }
                    })
            },
            //
            handleCancelChild() {
                this.tabDataI = this.$lodash.cloneDeep(this.copyDataI);
                this.otherVariable.isChildEdit = false;
                this.clickTimes = 0;
            },
            //
            handleNotAllowed() {
                if(this.otherVariable.isChildEdit) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.handleCancelChild();
                    }).catch(() => {
                        console.log('cnacel')
                    })
                }
            },
            isSaveChildRow() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.handleSaveChild();
                }).catch(() => {
                    console.log('cancel')
                    //this.handleCancelChild();
                })
            },
            //
            handleBatchEdit(column, event) {
                //column.label
            },
            //
            handleOtherTable(row) {
                if(this.otherVariable.isChildEdit) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }else {
                    this.showTable = !this.showTable;
                    if(this.showTable) {
                        this.isResize = false;
                        this.tableHeightI = this.tableHeightI - 150;
                        this.showTable = true;
                        this.$nextTick(() => {
                            this.isResize = true;
                        })
                    }else {
                        this.tableHeightI = this.tableHeightI + 150;
                    }
                    this.tabDataIII = [];
                    //
                    this.fetch('/oms/sls/SalesOrderPriceController/getMatPriceItem', {matPriceId:row.priceId})
                        .then(result => {
                            if (result.status === 200) {
                                this.tabDataIII = result.data.list;
                            }else {
                                this.tabDataIII = [];
                            }
                        })
                        .then(() => {
                            if(this.showTable) {
                                this.$refs['tabTableII'].initStatus();
                            }
                        })
                }
            },
            //
            getUnitList() {
                this.getAjax('/basedata/sys/DataDictionary/getDataDictionary?docCode=30126')
                    .then(result => {
                        if(result.data) {
                            result.data.forEach(item => {
                                item.dictValue = Number(item.dictValue)
                            });
                            this.unitList = this.formatArray(result.data, 'dictText', 'dictValue');
                        }
                    })
            },
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
    .menu-icon-finance {
        background-size: 27px !important;
        background-position: center;
    }
    .xs-icon-confirm {
        position: absolute;
        bottom: 2px;
        right: 119px;
    }
</style>



